function settingLanguage(lang){ 
    i18n.changeLanguage(lang);  
} 


function settingLanguageOnchange(lang){ 
	localStorage.setItem("onchangeLanguage",lang);
    i18n.changeLanguage(lang);  
	window.location.reload();
} 

