	  printReviewStar = (review) => {
		const row = [];
		for (var i = 0; i < review; i++) {
		  row.push(<i class="fas fa-star filled"></i>);
		}
		  for (var i = 0; i < (5-review); i++) {
		  row.push(<i class="fas fa-star "></i>);
		}
		return row;
	  };
	
	

      privateProfileUpdate =(e) => {
        e.preventDefault();  
		 
        if(this.state.name==""){
            alert('Please enter name.');
            return false;
        }

        if(this.state.surname==""){
            alert('Please enter name.');
            return false;
        }

        if(this.state.email==""){
            alert('Please enter email.');
            return false;
         }

         if(this.state.country==""){
            alert('Please enter country.');
            return false;
         }

         if(this.state.placeofbirth==null){
            alert('Please enter place of birth.');
            return false;
         }


         var healthcareValue = 0;
		 if(this.state.healthcare){
             healthcareValue = 1;
         }

         var universityValue = 0;
		 if(this.state.university){
            universityValue = 1;
         } 

         const customerData = {customer_id:localStorage.getItem('customer_id'),name :this.state.name , surname : this.state.surname, email : this.state.email , country : this.state.country ,  stdcode : this.state.stdcode  , mobile : this.state.mobile , city : this.state.city, dob : this.state.dob, streetaddress : this.state.streetaddress, zipcode : this.state.zipcode, consultationlanguage:this.state.consultationlanguage}
         console.log(this.state.idback);

         const data = new FormData(); 
         data.append('idfront',this.state.idfront); 
         data.append('idback',this.state.idback); 
         data.append('specialist_id',localStorage.getItem('specialist_id'));
         data.append('name',this.state.name);
         data.append('surname',this.state.surname);
         data.append('email',this.state.email);
         data.append('mobile',this.state.mobile);
         data.append('stdcode',this.state.stdcode);
         data.append('country',this.state.country);
         data.append('placeofbirth',this.state.placeofbirth);
         data.append('other',this.state.other);
         data.append('dob',this.state.dob); 
         data.append('healthcare',healthcareValue);
         data.append('university',universityValue);
		 data.append('language',universityValue);
         console.log(data);
 

		 axios.post(`/specilistAPI/updatePrivateSetting`,data)
		 .then(res => {
			 //this.setState({countryData : res.data});
		    console.log(res.data);
			
			if(res.data.Status) 
			{ 
				alert('Update successfully');
			}
			else 
			alert(res.data.Message);
			
	    	}).catch(function (error) {
			console.log(error);
		  });   
      }

